import axios from "axios";
import React, { Component } from "react";
import { IoIosArrowDown, IoIosArrowUp } from "react-icons/io";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import { Auth, Basic } from "../../../actions";
import { FC_GetDistricts } from "../../../actions/transfer.action";
import DashboardContainer from "../../../components/DashboardContainer/DashboardContainer";
import ExcelExport from "../../../components/ExcelExport/ExcelExport";
import ExportCSV from "../../../components/ExcelExport/ExportCSV";
import ExportReactCSV from "../../../components/ExcelExport/ExportReactCSV";
import Loading from "../../../components/Loading/Loading";
import { APP_URL } from "../../../config/app.config";
import { StoreState } from "../../../reducers";
import { DistrictTypes } from "../../../types/TransferTypes";
import { checkAccess, UsersAccess } from "../../../utils/access";
import { setAxiosToken } from "../../../utils/AxiosToken";
import { commaFy, search } from "../../../utils/functions";
import { SchoolsPostsRequests } from "../../DestrictRequest/Request/DistrictRequest";
import DistrictsLabel from "./DistrictsLabel";

export const GetDistrictPostsRequests = async (
  district_code: string,
  academic_year_id: string,
  setStatus: (loading: boolean, data: SchoolsPostsRequests | null) => void
) => {
  setStatus(true, null);
  try {
    setAxiosToken();
    const res = await axios.get<SchoolsPostsRequests | null>(
      `${APP_URL}/districtdistribution/peracademic/${district_code}/${academic_year_id}`
    );
    console.log("District Requests: ", res.data);
    setStatus(false, res.data);
  } catch (error: any) {
    setStatus(false, null);
    console.log("bb err: ", { ...error });
    console.log("bb err: ", error);
  }
};

interface PostsRequestsReportProps {
  auth: Auth;
  basic: Basic;
}
interface PostsRequestsReportState {
  loading: boolean;
  data: SchoolsPostsRequests | null;
  error: string;
  selected_district: string;
  districts: DistrictTypes[] | null;
  showDistrictsList: boolean;
  searchData: string;
  selectedSchool_code: string | null;
}

class _PostsRequestsReport extends Component<
  PostsRequestsReportProps,
  PostsRequestsReportState
> {
  constructor(props: PostsRequestsReportProps) {
    super(props);

    this.state = {
      loading: true,
      data: null,
      error: "",
      selected_district:
        this.props.auth.district !== null &&
        this.props.auth.role?.role_id === "3"
          ? this.props.auth.district.district_code
          : "",
      districts: null,
      showDistrictsList: false,
      searchData: "",
      selectedSchool_code:
        this.props.auth.school !== null && this.props.auth.role?.role_id === "2"
          ? this.props.auth.school.school_code
          : null,
    };
  }

  GetTotalHTrequests = () => {
    var total: number = 0;
    if (this.state.data !== null) {
      for (let item of this.state.data.academic_distributed) {
        total += parseInt(item.head_teacher_post_request);
      }
      return total.toString();
    }
    return "Loading...";
  };
  GetTotalDDEDistribution = () => {
    var total: number = 0;
    if (this.state.data !== null) {
      for (let item of this.state.data.academic_distributed) {
        total += parseInt(
          item.dde_post_distribution === null ? "0" : item.dde_post_distribution
        );
      }
      return total.toString();
    }
    return "Loading...";
  };

  GetDistricts = (district_code: string, academic_year_id: string) => {
    GetDistrictPostsRequests(
      district_code,
      academic_year_id,
      (loading: boolean, data: SchoolsPostsRequests | null) => {
        this.setState({ loading: loading, data: data });
        if (data === null && loading === false) {
          this.setState({
            loading: false,
            data: null,
          });
        }
      }
    );
  };

  componentDidMount = () => {
    if (this.props.auth.role?.role_id !== "3") {
      FC_GetDistricts(
        (status: boolean, data: DistrictTypes[] | null, msg: string) => {
          this.setState({ loading: true });
          if (data !== null) {
            this.setState({
              loading: false,
              districts: data,
            });
          } else {
            this.setState({
              loading: false,
            });
          }
        }
      );
    }
    // Get Districts Function
    if (
      this.props.auth.district !== null &&
      this.props.basic.academic_calender !== null &&
      this.props.auth.role?.role_id === "3"
    ) {
      this.GetDistricts(
        this.props.auth.district.district_code,
        this.props.basic.academic_calender.academic_year_id
      );
    }
  };
  render() {
    // Check access
    let access_list = this.props.auth.role?.access;
    let selected_district_name =
      this.props.auth.role?.role_id === "3"
        ? this.props.auth.district?.district_name
        : this.state.districts?.find(
            (itm) => itm.district_code === this.state.selected_district
          )?.district_name;
    if (checkAccess(access_list, UsersAccess.POSTS_REQUESTS_REPORT) === false) {
      return <Redirect to="/dashboard" />;
    }
    console.log("Last test: ", this.state.data);
    const data =
      this.state.data === null
        ? []
        : (search(
            this.state.selected_district === null
              ? this.state.data.academic_distributed
              : this.state.data.academic_distributed
                  .filter(
                    (itm) =>
                      (this.props.auth.role?.role_id === "3" &&
                        itm.district_code === this.state.selected_district) ||
                      (this.props.auth.role?.role_id === "2" &&
                        itm.school_code === this.state.selectedSchool_code)
                  )
                  .map((item, i) => ({
                    id: (i + 1).toString(),
                    school_code: item.school_code,
                    school_name: item.school_name,
                    position_name:
                      item.position_name + "( " + item.position_code + " )",
                    qualification_name: item.qualification_name,
                    existing_teachers: item.existing_post,
                    new_requests: item.head_teacher_post_request,
                    dde_requests:
                      item.dde_post_request === null
                        ? ""
                        : item.dde_post_request,
                    dde_post_distribution:
                      item.dde_post_distribution === null
                        ? "0"
                        : item.dde_post_distribution,
                    ht_request_reason: item.reason_name,
                    dde_comment:
                      item.dde_post_request_comment === null
                        ? ""
                        : item.dde_post_request_comment,
                  })),
            this.state.searchData
          ) as {
            id: string;
            school_code: string;
            school_name: string;
            position_name: string;
            qualification_name: string;
            existing_teachers: string;
            new_requests: string;
            dde_requests: string | null;
            ht_request_reason: string;
            dde_comment: string | null;
            dde_post_distribution: string;
          }[]);
    const headers = [
      { label: "#", key: "id" },
      { label: "Position name", key: "position_name" },
      { label: "Qualification name", key: "qualification_name" },
      { label: "Existing teachers", key: "existing_teachers" },
      { label: "HT Requests", key: "new_requests" },
      { label: "HT Request reason", key: "ht_request_reason" },
      { label: "DDE Requests", key: "dde_requests" },
      { label: "DDE posts distribution", key: "dde_post_distribution" },
      { label: "DDE comment", key: "dde_comment" },
    ];
    const wscols = [
      { wch: Math.max(...data.map((item) => item.id.length)) },
      { wch: Math.max(...data.map((item) => item.school_code.length)) },
      { wch: Math.max(...data.map((item) => item.school_name.length)) },
      { wch: Math.max(...data.map((item) => item.position_name.length)) },
      { wch: Math.max(...data.map((item) => item.qualification_name.length)) },
      { wch: Math.max(...data.map((item) => item.existing_teachers.length)) },
      { wch: Math.max(...data.map((item) => item.new_requests.length)) },
      { wch: Math.max(...data.map((item) => item.ht_request_reason.length)) },
      { wch: Math.max(...data.map((item) => item.dde_requests!.length)) },
      {
        wch: Math.max(...data.map((item) => item.dde_post_distribution.length)),
      },
      { wch: Math.max(...data.map((item) => item.dde_comment!.length)) },
    ];
    if (this.state.loading === true) {
      return (
        <DashboardContainer className="flex flex-col items-center justify-center">
          <Loading />
          <div>Loading, please wait...</div>
        </DashboardContainer>
      );
    }
    return (
      <div>
        <DashboardContainer>
          <div className="flex flex-row items-center justify-between gap-2 w-full mb-3">
            <div className="text-primary-800 text-2xl font-extrabold mb-5 flex flex-col">
              Posts requests Report in {selected_district_name} district
              <div className="flex flex-row items-center gap-3 font-bold text-black text-base">
                <span>Academic year:</span>
                <div className="truncate">
                  {this.props.basic.academic_calender?.academic_year_name}
                </div>
              </div>
            </div>
            <div className="flex flex-row justify-end items-center gap-2">
              <div className="bg-blue-200 rounded-lg px-2 py-1">
                <div className="text-sm font-bold">Total HT Requests</div>
                <div className="text-4xl font-extrabold">
                  {commaFy(parseInt(this.GetTotalHTrequests()))}
                </div>
              </div>
              <div className="bg-green-100 rounded-lg px-2 py-1">
                <div className="text-sm font-bold">Total DDE distribution</div>
                <div className="text-4xl font-extrabold">
                  {commaFy(parseInt(this.GetTotalDDEDistribution()))}
                </div>
              </div>
            </div>
          </div>

          <div className="flex flex-row items-center gap-3 justify-between">
            {this.props.auth.role?.role_id !== "3" && (
              <div className="flex flex-row items-center gap-3 w-full">
                {this.state.districts === null ? (
                  <div className="font-bold">Loading, please wait...</div>
                ) : (
                  <div className="w-full">
                    <div
                      onClick={() =>
                        this.setState({
                          showDistrictsList: !this.state.showDistrictsList,
                        })
                      }
                      className={`border font-bold ${
                        this.state.showDistrictsList === false
                          ? "border-gray-400 hover:border-primary-800 hover:bg-blue-100"
                          : "border"
                      } hover:text-primary-800 w-full rounded px-3 py-2 bg-white cursor-pointer flex flex-row items-center gap-3 justify-between`}
                    >
                      <div className="flex flex-row items-center gap-3">
                        {this.state.selected_district !== "" && (
                          <div className="rounded-full px-2 bg-blue-200 text-primary-800">
                            {selected_district_name}
                          </div>
                        )}
                        <span>
                          {this.state.selected_district === ""
                            ? "Choose a district"
                            : "Change district"}
                        </span>
                      </div>

                      <div>
                        {this.state.showDistrictsList === false ? (
                          <IoIosArrowDown className="text-xl" />
                        ) : (
                          <IoIosArrowUp className="text-xl" />
                        )}
                      </div>
                    </div>
                    {this.state.showDistrictsList === true && (
                      <DistrictsLabel
                        onChange={(value: string) => {
                          this.setState({ selected_district: value });
                          if (this.props.basic.academic_calender !== null) {
                            this.GetDistricts(
                              value,
                              this.props.basic.academic_calender
                                .academic_year_id
                            );
                          }
                        }}
                        districts={this.state.districts}
                        selected_district={this.state.selected_district}
                        setVisibility={(status: boolean) => {
                          this.setState({ showDistrictsList: status });
                        }}
                      />
                    )}
                  </div>
                )}
              </div>
            )}
            <input
              type="search"
              value={this.state.searchData}
              onChange={(e) => this.setState({ searchData: e.target.value })}
              className="bg-white rounded px-3 py-2 border border-gray-400 w-full"
              placeholder="Search..."
            />
            <div className="flex flex-row items-center gap-3">
              <ExportCSV
                csvData={data}
                fileName={`${selected_district_name}_Posts_Requests_Report_xlsx`}
                wscols={wscols}
                headers={[
                  "id",
                  "school_code",
                  "school_name",
                  "position_name",
                  "qualification_name",
                  "existing_teachers",
                  "new_requests",
                  "ht_request_reason",
                  "dde_requests",
                  "dde_post_distribution",
                  "dde_comment",
                ]}
                headerFormat={{
                  id: "#",
                  school_code: "School code",
                  school_name: "School name",
                  position_name: "Position name",
                  qualification_name: "Qualification name",
                  existing_teachers: "Existing teachers",
                  new_requests: "New HT requests",
                  ht_request_reason: "HT Requests reason",
                  dde_requests: "DDE Requests",
                  dde_post_distribution: "DDE Posts distribution",
                  dde_comment: "DDE Comment",
                }}
              />
              <ExportReactCSV
                csvHeaders={headers}
                csvData={data}
                fileName={`${selected_district_name}_Posts_Requests_Report_csv.csv`}
              />
            </div>
          </div>
          <div>
            {console.log("TEST DATA: ", data)}
            {data.length === 0 ? (
              this.props.auth.role?.role_id === "3" ||
              (this.state.selected_district !== "" &&
                this.props.auth.role?.role_id !== "2") ? (
                <div className="px-5 py-5 rounded-md mt-5 bg-gray-100 text-gray-400 font-extrabold text-center w-full text-2xl">
                  No data found!
                </div>
              ) : (
                <div
                  onClick={() =>
                    this.setState({
                      showDistrictsList: !this.state.showDistrictsList,
                    })
                  }
                  className="underline px-5 py-5 cursor-pointer rounded-md mt-5 bg-blue-50 text-gray-900 font-extrabold text-center w-full text-2xl"
                >
                  No district selected!
                </div>
              )
            ) : (
              <ExcelExport
                thead={
                  <thead className="w-full text-left bg-blue-50">
                    <tr>
                      <th className="border p-1 px-2 border-blue-200">#</th>
                      <th className="border p-1 border-blue-200">
                        School code
                      </th>
                      <th className="border p-1 border-blue-200">
                        School_name
                      </th>
                      <th className="border p-1 border-blue-200">
                        Position name
                      </th>
                      <th className="border p-1 border-blue-200">
                        Qualification name
                      </th>
                      <th className="border p-1 border-blue-200">
                        Existing teachers
                      </th>
                      <th className="border p-1 border-blue-200">
                        HT Requests
                      </th>
                      <th className="border p-1 border-blue-200">
                        HT Request reason
                      </th>
                      <th className="border p-1 border-blue-200">
                        DDE Requests
                      </th>
                      <th className="border p-1 border-blue-200">
                        DDE Posts distribution
                      </th>
                      <th className="border p-1 border-blue-200">
                        DDE comment
                      </th>
                    </tr>
                  </thead>
                }
                rowFormat={(
                  item: {
                    id: string;
                    school_code: string;
                    school_name: string;
                    position_name: string;
                    qualification_name: string;
                    existing_teachers: string;
                    new_requests: string;
                    dde_requests: string | null;
                    ht_request_reason: string;
                    dde_comment: string | null;
                    dde_post_distribution: string;
                  },
                  index: number
                ): JSX.Element => {
                  return (
                    <tr key={index}>
                      <td className="px-2 py-1 border">{index + 1}</td>
                      <td className="px-1 py-1 border">
                        <div className="flex flex-row items-center gap-1">
                          {/* {this.state.selectedSchool_code === null && (
                            <div className="text-3xl">
                              <div className="bg-blue-100 text-blue-700 hover:bg-blue-600 hover:text-white rounded-lg">
                                {this.state.selectedSchool_code === null ? (
                                  <BsPin />
                                ) : (
                                  <BsPinFill />
                                )}
                              </div>
                            </div>
                          )} */}
                          <div>{item.school_code}</div>
                        </div>
                      </td>
                      <td className="px-1 py-1 border">{item.school_name}</td>
                      <td className="px-1 py-1 border">{item.position_name}</td>
                      <td className="px-1 py-1 border">
                        {item.qualification_name}
                      </td>
                      <td className="px-1 py-1 border">
                        {item.existing_teachers}
                      </td>
                      <td className="px-1 py-1 border">{item.new_requests}</td>
                      <td className="px-1 py-1 border">
                        {item.ht_request_reason}
                      </td>
                      <td className="px-1 py-1 border">{item.dde_requests}</td>
                      <td className="px-1 py-1 border">
                        {item.dde_post_distribution}
                      </td>
                      <td className="px-1 py-1 border">{item.dde_comment}</td>
                    </tr>
                  );
                }}
                data={data}
              />
            )}
          </div>
        </DashboardContainer>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const PostsRequestsReport = connect(
  mapStateToProps,
  {}
)(_PostsRequestsReport);
