import React from "react";
import { CSVLink } from "react-csv";

interface ExportReactCSVprops {
  csvHeaders: {
    label: string;
    key: string;
  }[];
  csvData: any[];
  fileName: string;
}

const ExportReactCSV = (props: ExportReactCSVprops) => (
  <CSVLink
    headers={props.csvHeaders}
    data={props.csvData}
    filename={props.fileName}
  >
    <button
      className="bg-accent-900 text-white rounded px-3 py-2 text-center w-max"
      type="button"
      title="Export excel CSV"
    >
      Export CSV
    </button>
  </CSVLink>
);

export default ExportReactCSV;
