import React from "react";

// 3. create customers table component
interface TableProps {
  thead: React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLTableSectionElement>,
    HTMLTableSectionElement
  >;
  rowFormat: (item: any, index: number) => JSX.Element;
  data: any[];
  tableHead?: JSX.Element;
}
const ExcelExport = (props: TableProps) => {
  const TableDetails = props.data.map((item, index) =>
    props.rowFormat(item, index)
  );

  return (
    <div>
      {props.tableHead}
      <table className="striped bordered hover w-full text-sm mt-5">
        {props.thead}
        <tbody>{TableDetails}</tbody>
      </table>
    </div>
  );
};

export default ExcelExport;
